import React from 'react'
import styled from 'styled-components'
import { ChevronDownIcon, ChevronUpIcon, Text } from '@pancakeswap/uikit'
import { useTranslation } from 'contexts/Localization'

import chevron from './chevron.png'

export interface ExpandableSectionButtonProps {
  onClick?: () => void
  expanded?: boolean
}


const Wrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  svg {
    fill: ${({ theme }) => theme.colors.primary};
  }
`
const ChevronUpImage = styled.img`
  width: 18px;
  height: 18px;
`

const ExpandableSectionButton: React.FC<ExpandableSectionButtonProps> = ({ onClick, expanded }) => {
  const { t } = useTranslation()

  return (
    <Wrapper aria-label={t('Hide or show expandable content')} role="button" onClick={() => onClick()}>
      <Text color="#c70101" bold>
        {expanded ? t('Hide') : t('Details')}
      </Text>
      {/* {expanded ? <ChevronUpIcon /> : <ChevronDownIcon />} */}
      {expanded ? <ChevronUpImage src={chevron} style={{ transform: `rotate(180deg)` }} /> : <ChevronUpImage src={chevron} />}
    </Wrapper>
  )
}

ExpandableSectionButton.defaultProps = {
  expanded: false,
}

export default ExpandableSectionButton
